body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

header {
  width: 100%;
}

.top-bar {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px 40px;
  display: flex;
}

img {
  max-width: 40px;
  height: 40px;
  display: block;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  margin-left: 30px;
}

.nav-item:first-child {
  margin-left: 0;
}

.nav-link {
  color: #000;
  text-align: center;
  height: 40px;
  padding: 0;
  font-size: 12px;
  line-height: 40px;
  text-decoration: none;
  transition: color .2s;
  display: inline-block;
}

.nav-link:hover {
  color: #00acdc;
}

.nav-link.is-active {
  color: #00acdc;
  position: relative;
}

.nav-link.is-active:after {
  content: "";
  background: #00acdc;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}
